@import '~antd/dist/antd.less';
@import '~antd/lib/style/themes/default.less';
//@import 'theme.less';

.App {
    text-align: center;
}

#root {
    min-height: 100%;
    display: flex;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

ul, li {
    list-style: none;
    padding: 0;
    margin: 0;
}

@primary-color: teal;@border-color: #1DA57A;@link-color: teal;@success-color: #52c41a;@warning-color: #faad14;@error-color: #f5222d;@font-size-base: 14px;@heading-color: rgba(0, 0, 0, 0.85);@text-color: rgba(0, 0, 0, 0.65);@text-color-secondary: rgba(0, 0, 0, 0.45);@disabled-color: rgba(0, 0, 0, 0.25);@border-radius-base: 2px;@border-color-base: #d9d9d9;@box-shadow-base: 0 3px 6px -4px rgba(0, 0, 0, 0.12), 0 6px 16px 0 rgba(0, 0, 0, 0.08),0 9px 28px 8px rgba(0, 0, 0, 0.05);